import { FunctionComponent } from 'react'
import { Route, Switch } from 'react-router-dom'
import AutoSizer, { Size } from 'react-virtualized-auto-sizer'
import { TApp } from '../../../@types/data'
import { ROUTE_OPENED_APPS, ROUTE_ROOT } from '../../../config/routes'
import styles from './ContentPane.module.scss'

/**
 * Main component
 *
 * The content pane is made up of several components
 *
 * The main components:
 * - home app pages
 * - opened app
 * - app list
 *
 * These main components uses react router
 *
 * For example:
 * - '/'        : is the home page. It is always open?
 * - '/:appName : is the opened app name
 * - '/apps     : is the app list
 *
 * Home page consists of apps, folders and widgets
 * - folder     : contains apps. No nesting of folders. Uses one grid slot
 * - app        : an application that can be opened, deleted, analyzed. Uses on grid slot
 * - widget     : an app that is directly on the home page such as time, weather
 *
 * With widgets, either the page grid slots or the widget size needs to be restricted.
 * The widget size will be restricted
 */
// Component props
type TWidgetAppProps = {}
type TFolderAppProps = {}
type TAppProps = {
  app: TApp
  size: Size
}

type THomePaneProps = {
  apps: TApp[]
  size: Size
}
type TAppPaneProps = {}
type TOpenedAppsPaneProps = {}

type TContentPaneRouterProps = {
  apps: TApp[]
  size: Size
}
type TContentPaneProps = {
  apps: TApp[]
}

const InternalApp = ({ component }: any) => {
  return <button>{component}</button>
}

const ExternalApp = ({ link, component }: any) => {
  return (
    <a className={styles.externalApp} href={link}>
      {component}
    </a>
  )
}

// TODO
// Content types
export const WidgetApp = () => {}
export const FolderApp = () => {}
export const App: FunctionComponent<TAppProps> = ({ app, size }) => {
  const { icon: IconComponent, name, route } = app
  const { link, type } = route
  const component = (
    <div className={styles.app} style={{ width: size.width }}>
      <IconComponent className={styles.app__icon} {...size} />
      <div className={styles.app__title}>{name}</div>
    </div>
  )

  let WrapperComponent = null

  switch (type) {
    case 'external':
      WrapperComponent = ExternalApp
      break
    default:
    case 'internal':
      WrapperComponent = InternalApp
      break
  }
  return <WrapperComponent component={component} link={link} />
}

export const HomePane: FunctionComponent<THomePaneProps> = ({ apps, size }) => {
  return (
    <div className={styles.homePane}>
      <App app={apps[0]} size={{ height: 80, width: 80 }} />
    </div>
  )
}

// TODO
export const AppPane = () => {}
export const OpenedAppsPane = () => <div></div>

export const ContentPaneRouter: FunctionComponent<TContentPaneRouterProps> = ({
  apps,
  size,
}) => (
  <Switch>
    <Route path={ROUTE_ROOT}>
      <HomePane apps={apps} size={size} />
    </Route>
    <Route path={ROUTE_OPENED_APPS}>
      <OpenedAppsPane />
    </Route>
  </Switch>
)

export const ContentPane: FunctionComponent<TContentPaneProps> = ({ apps }) => (
  <div className={styles.contentPane}>
    <AutoSizer>
      {(size) => <ContentPaneRouter apps={apps} size={size} />}
    </AutoSizer>
  </div>
)
